import { useEffect, useState } from 'react';
import './portfolio.scss';
import PortfolioList from '../portfolioList/PortfolioList';
import {
  featuredPortfolio,
  webPortfolio,
  backendPortfolio,
  otherPortfolio,
} from '../../data';

const Portfolio = () => {
  const [selected, setSelected] = useState('featured');
  const [data, setData] = useState([]);

  const list = [
    {
      id: 'featured',
      title: 'My Projects',
    },
    {
      id: 'backend',
      title: 'Team Projects',
    },
    {
      id: 'web',
      title: 'Certification',
    },
    {
      id: 'other',
      title: 'Others',
    },
  ];

  // jika terjadi perubahan pada state (mungkin)
  useEffect(() => {
    switch (selected) {
      case 'featured':
        setData(featuredPortfolio);
        break;
      case 'web':
        setData(webPortfolio);
        break;
      case 'backend':
        setData(backendPortfolio);
        break;
      case 'other':
        setData(otherPortfolio);
        break;
      default:
        setData(featuredPortfolio);
    }
  }, [selected]);

  return (
    <div className="portfolio" id="portfolio">
      <h1>Portfolio</h1>
      <ul>
        {list.map((item) => (
          <PortfolioList
            title={item.title}
            id={item.id}
            active={selected === item.id}
            setSelected={setSelected}
          />
        ))}
      </ul>
      <div className="container">
        {data.map((d) => (
          <div className="item">
            <img src={d.img} alt="" />
            <h3>{d.title} </h3>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Portfolio;
